body {
  background-color: #33658A;
}

#loginPageMain {
  background-color: #86BBD8;
  display: flex;
  justify-content: center;
  margin-top: 160px;
  padding: 3%;
}

#loginFormContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 70%;
  height: 30%;
}

#loginFormContainer img {
  width: 250px;
  margin-bottom: 1.5%;
}

#loginForm {
  background-color: #33658A;
  border-radius: 15px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  display: flex;
  flex-direction: column;
  width: 45%;
  align-items: center;
  padding: 4% 8%;
}

#loginForm label {
  text-align: center;
  width: 400px;
}

#loginForm input[type = "text"], #loginForm input[type = "email"]  {
  border: solid 1.5px #189AB4;
  border-radius: 4px;
  font-size: 1.15em;
  width: 65%;
  margin-bottom: 15px;
  padding: 2%;
  -webkit-transition: 0.4s width ease-in-out;
  transition: 0.4s width ease-in-out;
}

#loginForm input[type = "text"]:focus, #loginForm input[type = "email"]:focus  {
  background-color: #D4F1F4;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  outline: none;
  width: 90%;
}

#loginBtn {
  background-color: #75E6DA;
  color: #05445E;
  border: none;
  border-radius: 5px;
  width: 40%;
  cursor: pointer;
  font-size: 1.2em;
  margin-top: 2%;
  padding: 2.5%;
  -webkit-transition: 0.4s width ease-in-out;
  transition: 0.4s width ease-in-out;
}

#loginBtn:hover {
  background-color: #D4F1F4;
  color: black;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  width: 55%;
}

#loginBtn:disabled {
  background-color: rgba(170, 170, 170, 0.466);
  color: ghostwhite;
  border: none;
  border-radius: 8px;
  width: 30%;
  font-size: 1em;
  padding: 2.5%;
  -webkit-transition: 0.4s width ease-in-out;
  transition: 0.4s width ease-in-out;
}

#loginBtn:disabled:hover {
  background-color: rgba(170, 170, 170, 0.466);
  color: ghostwhite;
  cursor: default;
  box-shadow: none;
  width: 30%;
}

#settingsBtn {
  background-color: #f8ca74;
  border: none;
  border-radius: 5px;
  width: 10%;
  cursor: pointer;
  font-size: 1.2em;
  margin-top: 1.5%;
  padding: 0.5%;
  -webkit-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
}

#settingsBtn:hover {
  background-color: #faba44;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
