}

#questionCard {
  background-color: rgb(242, 229, 205);
  border-radius: 50px 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 1%;
  margin-bottom: 3%;
  padding: 1% 3%;
}

#questionCard h3 {
  color: #2F4858;
  text-decoration: overline;
  font-size: 1.7em;
  text-align: center;
  margin-top: 1%;
}

#questionCard p {
  color: black;
  font-size: 1.35em;
  text-align: justify;
