}

#answerBtnsContainer {
  background-color: #33658A;
  border-radius: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  width: 60%;
  padding: 2% 2% 1% 2% ;
}

.correctAnswer, .incorrectAnswer {
  background-color: #D4F1F4;
  color: #05445E;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1.4em;
  width: 320px;
  height: 120px;
  margin-bottom: 5%;
  padding: 5%;
  -webkit-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
}

.correctAnswer:hover, .incorrectAnswer:hover {
  background-color: #D6FFB7;
  color: black;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.correctAnswer:disabled, .incorrectAnswer:disabled {
  background-color: rgba(170, 170, 170, 0.466);
  color: ghostwhite;
}

.correctAnswer:disabled:hover, .incorrectAnswer:disabled:hover {
  background-color: rgba(170, 170, 170, 0.466);
  color: ghostwhite;
  cursor: default;
  box-shadow: none;
}

#cardContainer1 h4 {
  background-color: #D6FFB7;
